import React, { useState } from "react";

import Input from "./components/Input";
import Display from "./components/Display";
import NewInputBtn from "./components/NewInputBtn";
import "./App.css";

const getAgeRequest = async (name) => {
  const response = await fetch(`https://api.agify.io/?name=${name}`);
  const data = await response.json();
  return data.age;
};

const App = (props) => {
  const [nameInput, setNameInput] = useState("");
  const [age, setAge] = useState("");
  const [showInput, setShowInput] = useState(true);
  const [showDisplay, setShowDisplay] = useState(false);

  const hideInput = () => {
    setShowInput(true);
    setShowDisplay(false);
  };

  const handleInput = (event) => {
    setNameInput(event.target.value);
  };

  const getAge = async () => {
    const ageData = await getAgeRequest(nameInput);
    setAge(ageData);
    setShowInput(false);
    setShowDisplay(true);
  };

  return (
    <div className="wrapper">
      <h1>Let's see how old you are</h1>
      {showInput ? (
        <Input
          name={nameInput}
          handleChange={handleInput}
          onFormSubmit={getAge}
        />
      ) : (
        <NewInputBtn handleClick={hideInput} />
      )}
      {showDisplay && <Display name={nameInput} age={age} />}
    </div>
  );
};

export default App;
