import React from "react";

import classes from "./Display.module.css";

const Display = (props) => {
  return (
    <div className={classes.display__wrapper}>
      <div className={classes.display__col1}>
        <p>Based on the entered name, the AI thinks that {props.name} is</p>
      </div>
      <div className={classes.display__col2}>
        <p>{props.age}</p>
      </div>
    </div>
  );
};

export default Display;
